#home {
    height: 100%;
    width: 100%;
    padding: 2.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

.home-signs {
    position: relative;
    top: -30px;
    color: steelblue;
    text-align: center;
}

.home-signs > h1 {
    font-size: 45px;
}

.home-signs > p {
    font-size: 25px;
}

#writer {
    font-weight: bold;
}

.home-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
}

.home-icon-single {
    color: inherit;
    padding: 7px;
}

.svg-inline--fa {
    transition: all .2s ease-in-out;
}

.svg-inline--fa:hover {
    transform: scale(1.05);
    color:steelblue;
}

.circle-small {
    display: inline-block;
    border-radius: 30px;
    box-shadow: 0px 0px 5px #888;
    padding: 0.5em 0.6em;
    width: 1.4rem !important;
}

@media only screen and (max-width: 690px) {
    #home {
        padding: 2rem;
        overflow-x: hidden; 
    }
    
    .home-signs {
        top: -20px;
        color: steelblue;
        text-align: center;
    }
    
    .home-signs > h1 {
        font-size: 40px;
    }
    
    .home-signs > p {
        font-size: 20px;
    }
    
    .home-icons {
        height: 35px;
    }
    
    .home-icon-single {
        padding: 5px;
    }
    
    .circle-small {
        padding: 0.45em 0.55em;
        width: 1.1rem !important;
    }
}