#feed-wrapper {
    position: absolute;
    height: 100%;
    width: 100%;
    min-width: 480px;
    box-sizing: border-box;
    top: 0;
    left: 0;
    padding: 0 16.8rem;
    /* overflow: auto; */
    z-index: 1;
}

@media only screen and (max-width: 1024px) {
    #feed-wrapper {
        padding: 0 4rem;
        /* overflow-x: hidden; */
    }
}

@media only screen and (max-width: 690px) {
    #feed-wrapper {
        padding: 0 2rem;
    }
}